.moto {
  width: 50%;
  margin-bottom: 170px;
  padding-top: 82px;
  padding-left: 84px;

  @include viewport--md {
    padding-left: 0;
  }

  @include viewport--sm {
    width: 100%;
  }

  &__header {
    margin: 0;
    margin-bottom: 20px;

    @include font-scale(50);
    line-height: 124%;
    font-weight: normal;
  }

  &__text {
    margin: 0;
    margin-bottom: 25px;
  }
}

